import {FastSchemeValidator} from "../FastSchemeValidator";
import ListFieldReorder from "../formFields/ListFieldReorder/ListFieldReorder";

/**
 *
 * More info at https://github.com/chomamateusz/list-field-reorder-demo/blob/master/src/schemas/array-of-arrays.js
 * More info at https://codesandbox.io/embed/bold-hill-2ol405jn6r
 *
 * @returns {JSONSchemaBridge}
 * @constructor
 */
const ProjectInfoSchema = () => {
    return FastSchemeValidator({
        type: 'object',
        properties: {
            key: {
                title: "Id",
                type: "string",
                description: "ID",
                options: {
                    hidden: true
                }
            },
            researchGroup: {
                title: "Nombre del grupo",
                type: "string",
                description: "Research group name", uniforms: {
                    //labelCol: {span: 8}
                }
            },
            researchGroupCode: {
                title: "Código del grupo",
                type: "string",
                description: "Research group code", uniforms: {
                    //labelCol: {span: 8}
                }
            },
            researchName: {
                title: "Nombre de investigación",
                type: "string",
                description: "Research name",
                uniforms: {
                    labelCol: {span: 8}
                }
            },
            researchNameCode: {
                title: "Código de investigación",
                type: "string",
                description: "Research name code", uniforms: {
                    labelCol: {span: 8}
                }
            }, fps: {
                title: "FPS",
                type: "integer",
                description: "Video fps",
/*                defaultValue: 21,*/
                allowedValues: [21, 25],
                uniforms: {
                    labelCol: {span: 8}
                }
            },
            numVideos: {
                title: "Default video grid size",
                type: "integer",
                description: "Videos",
                /*defaultValue: 2,*/
                allowedValues: [1, 2, 3]
            },
            userProfiles: {
                //title: "Investigadores",
                type: "array",
                /*"format": "table",*/
                items: {
                    title: "Perfil",
                    type: "object",
                    id: "profile",
                    properties: {
                        key: {
                            title: "Id",
                            type: "string",
                            description: "ID",
                            options: {
                                "hidden": true
                            }
                        },
                        registerCode: {
                            title: "Código",
                            type: "string",
                            description: "Código para el registro observacional",
                            options: {
                                hidden: true
                            },
                            uniforms: {
                                labelCol: {span: 8}
                            }
                        },
                        userName: {
                            title: "Nombre completo",
                            type: "string",
                            description: "User name",
                            uniforms: {
                                labelCol: {span: 8}
                            }
                        },
                  /*      email: {
                            title: "Email",
                            type: "string",
                            description: "Email",
                            //format: "email",
                            uniforms: {
                                labelCol: {span: 8}
                            }
                        },*/
                        registerAmount: {
                            title: "Observaciones",
                            type: "integer",
                            default: 0,
                            options: {
                                inputAttributes: {
                                    "class": "disabled"
                                }
                            }
                        }
                    }
                },
                uniforms: {
                    component: ListFieldReorder
                },
            }
        }
    });
};
export default ProjectInfoSchema;