/**
 * Base component for profile management: Register order and users!
 *
 * > https://levelup.gitconnected.com/react-redux-hooks-useselector-and-usedispatch-f7d8c7f75cdd
 * ojo:https://stackoverflow.com/questions/54632520/how-to-fetch-data-by-existing-redux-action-with-hooks
 * Uses hooks with redux in a simple component
 * Easy and nice example
 * @returns {*}
 */
export default function () {
    const dispatch = useDispatch();
    const model = useSelector(state => state.lince.profile);
    useEffect(() => {
        dispatch(fetchProfileInfo());
        dispatch(fetchSessionParameters());
    }, [dispatch])
    const onSave = (model) => {
        dispatch(saveProfileInfo(model));
    }
    const _getSchema = () => {
        return ProjectInfoSchema(); //Todo 2020: add i18n support
    }
    //const model = useSelector(state => state.lince.profile);
    const {rowStyle, colStyle, gutter} = basicStyle;
    return (
        <Provider store={store}>
            <StyledLayoutContentWrapper>
                <LinceLayoutContent>
                    <AutoForm schema={_getSchema()}
                              label={false}
                              model={model}
                              placeholder={true}
                              onSubmit={model => onSave(model)}>
                        <PageHeader><IntlMessages id="project-info"/>&#160;<SubmitField value=<IntlMessages
                                                                                        id="update"/>/></PageHeader>
                        <Row style={rowStyle} gutter={gutter} justify="start">
                            <Col md={16} sm={16} xs={24} style={colStyle}>
                                <Box title={<IntlMessages id="project"/>}>
                                    <HiddenField name="key"/>
                                    <Row>
                                        <Col md={8}><b><IntlMessages id="project-group"/></b></Col>
                                        <Col md={8}><LinceAutofield name="researchGroupCode" width={"300px"}/></Col>
                                        <Col md={8}><LinceAutofield name="researchGroup"/></Col>
                                    </Row>
                                    <Row>
                                        <Col md={8}><b><IntlMessages id="project-name"/></b></Col>
                                        <Col md={8}><LinceAutofield name="researchNameCode"/></Col>
                                        <Col md={8}><LinceAutofield name="researchName"/></Col>
                                    </Row>
                                    <Row>
                                        <Col md={24}> <Divider/></Col>
                                    </Row>
                                    <Row>
                                        <Col md={24}><b><IntlMessages id="project-observers"/></b></Col>
                                        <Col md={12}><IntlMessages id="username"/></Col>
                                        <Col md={12}><IntlMessages id="userObservations"/></Col>
                                    </Row>
                                    <ListFieldReorder name="userProfiles">
                                        <ListItemField name="$" allowOrdering={false}>
                                            <NestField showInlineError name={""}>
                                                <Row>
                                                    <Col md={12}>
                                                        <LinceAutofield name="userName"/>
                                                    </Col>
                                                    <Col md={6}>
                                                        <TextField name="registerAmount" disabled={true}/>
                                                    </Col>
                                                </Row>
                                            </NestField>
                                        </ListItemField>
                                    </ListFieldReorder>
                                </Box>
                            </Col>
                            <Col md={8} sm={8} xs={24} style={colStyle}>
                                <Box title={<IntlMessages id="project-settings"/>}>
                                    <Row>
                                        <Col md={8}>FPS</Col>
                                        <Col md={16}><AutoField name="fps" style={{width: '100%'}}/></Col>
                                    </Row>
                                    <Row>
                                        <Col md={8}><IntlMessages id="videosPerRow"/></Col>
                                        <Col md={16}><AutoField name="numVideos" style={{width: '100%'}}/></Col>
                                    </Row>
                                </Box>
                            </Col>
                        </Row>
                    </AutoForm>
                </LinceLayoutContent>
            </StyledLayoutContentWrapper>
        </Provider>
    );
}
